*,*::before,*::after {
    box-sizing: border-box;
    margin : 0;
    padding : 0;
    overflow: hidden;

    color : white;
    font-family: 'Montserrat', sans-serif;
}

body {
    background-color: #222;
}

.PageWrapper {
    width : 100vw;
    height : 100vh;
    overflow: hidden;
    display: grid;
}